.review {
  padding-bottom: 40px;
}
.review__content {
  display: flex;
}
.review__image-wrapper {
  flex-shrink: 0;
}
.review__image-wrapper img {
  display: block;
}
.review__text {
  border: 5px solid #FFCD00;
  padding: 35px 22px 22px 40px;
  min-height: 262px;
}
.review__text p {
  margin: 0;
}

@media (max-width: 767px) {
  .review__content {
    display: flex;
    flex-direction: column;
  }
  .review__text {
    min-height: auto;
    padding: 20px;
  }
}